import Vue from 'vue'
import Router from 'vue-router'
import App from './App.vue'
import store from './store'

Vue.config.productionTip = false
Vue.use(Router)

const router = new Router({
  routes: [
    { path: '/home', component: require('./views/home.vue').default },
    { path: '/items', component: require('./views/items.vue').default },
    { path: '/', redirect: '/home' },
    { path: '*', component: require('./views/notfound.vue').default }
  ]
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
